import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Plano_acao_nc } from '../../../modelo/Plano_acao_nc'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'plano_acao_nc-dados-edit',   templateUrl: 'plano_acao_nc_dadosEdit.component.html' }) export class Plano_acao_ncDadosEditComponent implements OnInit {    plano_acao_nc: Plano_acao_nc;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.plano_acao_nc.Plano_acao_nc";                 let id = +params['id'];         this.plano_acao_nc = new Plano_acao_nc();         this.plano_acao_nc.cd_plano_acao_nc = id;         o.objetoJson = this.plano_acao_nc;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a plano_acao_nc. ';       }      this.plano_acao_nc = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


